import React from "react";
import '../../assets/css/slideshow.css';
//import Loadable from "@loadable/component";
import Carousel, { slidesToShowPlugin, arrowsPlugin } from '@brainhubeu/react-carousel';
import '@brainhubeu/react-carousel/lib/style.css';
import LeftArrow from "../../assets/images/arrow_left.png";
import RightArrow from "../../assets/images/arrow_right.png";
import * as mainStyles from '../../assets/css/main.module.css';

const ImageCarousel = ({pictures}) => {

    const mobilePlugins = [
        'arrows',
        'infinite',
        {
            resolve: arrowsPlugin,
            options: {
            arrowLeft: <img src={LeftArrow} alt="Previous slide"/>,
            arrowLeftDisabled:<img src={LeftArrow}alt="Previous slide"/>,
            arrowRight: <img src={RightArrow} alt="Next slide"/>,
            arrowRightDisabled: <img src={RightArrow} alt="Next slide"/>,
            addArrowClickHandler: true,
            }
        }
    ]

    const tabletPlugins = [
        'centered',
        'arrows',
        'infinite',
        {
            resolve: slidesToShowPlugin,
            options: {
                numberOfSlides: 1.1,
            }
        },
        {
            resolve: arrowsPlugin,
            options: {
                arrowLeft: <img src={LeftArrow} alt="Previous slide"/>,
                arrowLeftDisabled:<img src={LeftArrow}alt="Previous slide"/>,
                arrowRight: <img src={RightArrow} alt="Next slide"/>,
                arrowRightDisabled: <img src={RightArrow} alt="Next slide"/>,
            addArrowClickHandler: true,
            }
        }
    ]

    const desktopPlugins = [
        'centered',
        'arrows',
        'infinite',
        {
            resolve: slidesToShowPlugin,
            options: {
                numberOfSlides: 1.2,
            }
        },
        {
            resolve: arrowsPlugin,
            options: {
                arrowLeft: <img src={LeftArrow} alt="Previous slide"/>,
                arrowLeftDisabled:<img src={LeftArrow}alt="Previous slide"/>,
                arrowRight: <img src={RightArrow} alt="Next slide"/>,
                arrowRightDisabled: <img src={RightArrow} alt="Next slide"/>,
            addArrowClickHandler: true,
            }
        }
    ]

    return(
        <Carousel className={mainStyles.carousel}
            plugins={[
                'centered',
                'arrows',
                'infinite',
                {
                    resolve: slidesToShowPlugin,
                    options: {
                        numberOfSlides: 1.4,
                    }
                },
                {
                    resolve: arrowsPlugin,
                    options: {
                        arrowLeft: <img src={LeftArrow} alt="Previous slide"/>,
                        arrowLeftDisabled:<img src={LeftArrow}alt="Previous slide"/>,
                        arrowRight: <img src={RightArrow} alt="Next slide"/>,
                        arrowRightDisabled: <img src={RightArrow} alt="Next slide"/>,
                    addArrowClickHandler: true,
                    }
                }
            ]}
            breakpoints={{
                1824: {
                    plugins: desktopPlugins
                },
                1024: {
                    plugins: tabletPlugins
                },
                480: {
                    plugins: mobilePlugins
                }
            }}
        >
            {pictures.map((picture,index) =>(
                <div className={mainStyles.imagecontainer} key={picture}>
                    <img src={picture} alt={`slide ${index + 1}`}/>
                </div>
            ))}
        </Carousel>
    );
};

//const LoadableImageCarousel = Loadable(() => import('./ImageCarousel'));

export default ImageCarousel;